import BigInt from 'big-integer';
import Hash from './hash';
import { getBig, getArray } from './bnInput';
import Utils from './utils';
import C25519Point from './c25519Point';
import C25519Key from './c25519Key';

/**
 * @param {Uint8Array|string} message 
 * @param {C25519Key} key
 * @returns {Uint8Array}
 */
export function sign(message, key) {
    var buff = typeof message === 'string' ? Buffer.from(message, 'utf-8') : message;
    var [r, s] = signBase(getM(buff), key);
    return map(r, s);
}

/**
 * @param {Uint8Array|string} message
 * @param {Uint8Array} signature
 * @param {C25519Key} key
 */
export function verify(message, signature, key) {
    if (!signature || signature.length != 64)
        return false;

    var buff = typeof message === 'string' ? Buffer.from(message, 'utf-8') : message;
    var [r, s] = unmap(signature);
    return verifyBase(getM(buff), r, s, key);
}

/**
 * @param {BigInt.BigInteger} r
 * @param {BigInt.BigInteger} s
 */
export function map(r, s) {
    var bufferR = getArray(r);
    var bufferS = getArray(s);

    var buffer = Buffer.alloc(64);
    buffer.set(bufferR, 32 - bufferR.length);
    buffer.set(bufferS, 64 - bufferS.length);

    return buffer;
}
/** @param {Uint8Array} signature */
export function unmap(signature) {
    return [getBig(signature.slice(0, 32)), getBig(signature.slice(32))];
}

/**
 * @param {BigInt.BigInteger} m
 * @param {C25519Key} key
 */
function signBase(m, key)
{
    var k, r, s;
    do {
        do {
            k = Utils.random(BigInt.one, C25519Point.n.subtract(BigInt.one));
            r = C25519Point.g.times(k).x.mod(C25519Point.n);
        } while (r.equals(BigInt.zero));
        var kInv = k.modInv(C25519Point.n);
        s = key.x.times(r).add(m).mod(C25519Point.n).times(kInv).mod(C25519Point.n)
    } while (s.equals(BigInt.zero));
    return [r, s];
}

/**
 * @param {BigInt.BigInteger} m
 * @param {BigInt.BigInteger} r
 * @param {BigInt.BigInteger} s
 * @param {C25519Key} key
 */
function verifyBase(m, r, s, key) {
    var w = s.modInv(C25519Point.n);
    var u1 = m.times(w).mod(C25519Point.n);
    var u2 = r.times(w).mod(C25519Point.n);
    var v = C25519Point.g.times(u1).add(key.y.times(u2)).x.mod(C25519Point.n);

    return v.equals(r);
}

/** @param {Uint8Array} message */
function getM(message)
{
    const size = Math.ceil(C25519Point.n.bitLength().valueOf() / 8);
    return getBig(Hash.shaBuffer(message).slice(0, size));
}
