import createHash from 'create-hash';
import createHmac from 'create-hmac';
import { createHash as hashNode } from 'crypto';
import utils from './utils.js';

export default class Hash {
    static sha(message) {
        return Hash.shaBuffer(message).toString('base64');
    }

    static shaBuffer(message) {
        var data = !utils.isBuffer(message) ? Buffer.from(message) : message;
        return createHash('sha256').update(data).digest();
    }

    static sha512Buffer(message) {
        var data = !utils.isBuffer(message) ? Buffer.from(message) : message;
        return createHash('sha512').update(data).digest();
    }

    static hmac(data, secret) {
        return Hash.hmacBuffer(data, secret).toString('base64');
    }
    
    static hmacBuffer(data, secret) {
        secret = !utils.isBuffer(secret) ? Buffer.from(secret) : secret;
        return createHmac('sha256', secret).update(data).digest();
    }

    static ripeBuffer(message) {
        var data = !utils.isBuffer(message) ? Buffer.from(message) : message;
        return hashNode('ripemd160').update(data).digest();
    }
}