/** @typedef {Uint8Array | string | number | BigInt.BigInteger| BN } BNInput */
import BigInt from 'big-integer';
import BN from 'bn.js';

/** @param {BNInput} number
 *  @returns {BN} */
export function getBN(number) {
    if (number instanceof Uint8Array || typeof number == "string" || typeof number == "number")
        return new BN(number)

    if (number instanceof BN || BN.isBN(number))
        return number;

    if (number != null)
        return new BN(number.toArray(256).value);

    // @ts-ignore
    return number;
};

/**
 * @param {BNInput} number
 * @returns { BigInt.BigInteger }
 */
export function getBig(number) {
    if (number instanceof BN || BN.isBN(number))
        return BigInt.fromArray(number.toArray('be'), 256, false);
    else if (typeof number === "number")
        return BigInt(number);
    else if (typeof number === "string")
        return BigInt(number);
    else if (number instanceof Uint8Array)
        return BigInt.fromArray(Array.from(number), 256, false);
    else
        return number;
};

/** @param {BigInt.BigInteger|BN} number */
export function getArray(number) {
    if (number instanceof BN || BN.isBN(number))
        return Buffer.from(number.toArray('be'));
    else
        return Buffer.from(number.toArray(256).value);
}