import assert from 'assert';
import BigInt from 'big-integer';
import secretShare from './secretShare.js';
import utils, { constants } from './utils.js';
import Hash from './hash.js';
import PHelp from './phelp.js';


export let params = class Params {
    static get p() { return BigInt("", 16); }
    static get g() { return BigInt("", 16); }
    static get q() { return Params.p.minus(BigInt.one).shiftRight(1); }
};

export default class ElGamalKey {
    /**
     * 
     * @param {boolean} privated 
     * @param {bigInt.BigInteger} p
     * @param {bigInt.BigInteger} q
     * @param {bigInt.BigInteger} g
     * @param {bigInt.BigInteger} key
     * @param {bigInt.BigInteger} id
     */
    constructor(privated, p, q, g, key, id = undefined) {
        this.privated = privated;
        this.p = p;
        this.q = q;
        this.g = g;
        this.key = key; //represent the secret in elgammal and the y in secret sharing
        this.id = id; //represent the x value in secret sharing
    }

    get size() {
        return this.p.bitLength().valueOf();
    }

    createPublic() {
        const key = this.g.modPow(this.key, this.p);
        return new ElGamalKey(false, this.p, this.q, this.g, key);
    }

    share(number, threshold) {
        const [xs, ys] = secretShare.share(this.key, number, threshold, this.q);
        return ys.map((yi, i) => new ElGamalKey(this.privated, this.p, this.q, this.g, yi, xs[i]));
    }

    static combine(shares) {
        const first = shares[0];
        const [ids, keys] = [shares.map(sh => sh.id), shares.map(sh => sh.key)]
        const key = secretShare.interpolate(ids, keys, first.q);

        return new ElGamalKey(first.privated, first.p, first.q, first.g, key)
    }

    toString() {
        const values = [this.p, this.g, this.key];
        if (this.id)
            values.push(this.id);

        const bytes = utils.encodeByteArray(values, this.size, true);
        var checksum = Hash.shaBuffer(bytes)[0];
        const falgs = ((this.privated ? 1 : 0) << 1 | (this.id ? 1 : 0)) << 6 | (checksum >> 2);

        var size = Math.ceil(this.size / 8);
        bytes.unshift(size & 0xff);
        bytes.unshift((size >> 8) & 0xff);
        bytes.unshift(falgs);
        
        return Buffer.from(bytes).toString('base64');
    }

    static from(text) {
        const [flags, s0, s1, ...bytes] = Buffer.from(text, 'base64');
        const privated = (flags & 0b10000000) > 0;
        const numbers = (flags & 0b1000000) ? 4 : 3;
        var checksum = Hash.shaBuffer(bytes)[0];
        var size = (s0 << 8) | s1

        assert(bytes.length == numbers * size && checksum >> 2 === (flags & 63) , 'Malformed Key');

        const [p, g, key, id ] = utils.decode(bytes, bytes.length * 8 / numbers);
        const q = p.minus(constants.one).divide(constants.two);

        return new ElGamalKey(privated, p, q, g, key, id);
    }

    static generate(bits = 256, confidence = 32) {
        const [p, q] = utils.randomPrime(bits, confidence);
        const g = utils.getPrimitiveRoot(p).modPow(constants.two, p);
        
        const x = utils.random(constants.one, q.subtract(constants.one));
        const y = g.modPow(x, p);

        return [new ElGamalKey(true, p, q, g, x), new ElGamalKey(false, p, q, g, y)];
    }

    /**
     * @returns {Promise<ElGamalKey[]>}
     */
    static generateAsync(bits = 256, confidence = 32) {
        return PHelp.run(() => ElGamalKey.generate(bits, confidence));
    }

    /**
     * @param {string|Uint8Array} key
     */
    static seed(key) {
        const [p, q, g] = [params.p, params.q, params.g]

        const bytes = p.bitLength().valueOf() / 8;
        const x = seedPrivate(key, bytes);
        const y = g.modPow(x, p);

        return [new ElGamalKey(true, p, q, g, x), new ElGamalKey(false, p, q, g, y)];
    }
}

/**
 * @param {string|Uint8Array} key
 * @param {number} size
 */
function seedPrivate(key, size) {
    var bytes = [...Array(Math.ceil(size / 64))].map(() => (key = Hash.sha512Buffer(key)));
    return BigInt.fromArray(Array.from(Buffer.concat(bytes).slice(0, size)), 256, false);
}
