import EcPointBase from './ecPointBase';
import EcCurve from './ecCurve';
import elliptic from 'elliptic';
const curve = EcCurve.custom25519();

export default class C25519Point extends EcPointBase {
    static get infinity() { return new C25519Point(); }
    static get p() { return curve.pCasted; }
    static get n() { return curve.nCasted; }
    static get g() { return new C25519Point(curve.g); }

    get _curve() { return curve; }
    
    /**
     * @param {import('./bnInput').BNInput|elliptic.curve.base.BasePoint} x
     * @param {import('./bnInput').BNInput} y
     */
    constructor(x = null, y = null) {
        super(x, y);
    }

    /**
     * @param {Uint8Array} data
     * @returns {C25519Point}
     */
    static from(data, compact = false) {
        return Object.setPrototypeOf(super._from(curve, data, compact), C25519Point.prototype);
    }

    /**
     * @param {string} message
     * @returns {C25519Point}
     */
    static fromString(message) {
        return Object.setPrototypeOf(super._fromString(curve, message), C25519Point.prototype);
    }
}
