import BigInt from 'big-integer';
import utils from './utils';

export default class ByteSerializer {
    /**
     * @param {Uint8Array} bytes
     * @returns {bigInt.BigInteger[]}
     */
    static DeserializeNumbers(bytes, countSize = 1) {
        return ByteSerializer.deserializeBytes(bytes, countSize).map(data => BigInt.fromArray(Array.from(data), 256, false))
    }

    /**
     * @param {Uint8Array} bytes
     * @returns {Uint8Array[]}
     */
    static deserializeBytes(bytes, countSize = 1) {
        if (bytes === null || bytes.length < 1 || (bytes[0] === 0 && bytes.length > 1))
            throw new Error("Format Error");

        var numItems = bytes[0];
        if (numItems === 0)
            return [];

        var lenIdx = 1;
        var numbers = []; 
        for (var i = 0; i < numItems; i++)
        {
            if (lenIdx >= bytes.length || lenIdx + countSize > bytes.length)
                throw new Error("Format Error");

            var nextBytes = ToInt(bytes.slice(lenIdx).slice(0, countSize));

            if (lenIdx + countSize + nextBytes > bytes.length)
                throw new Error("Format Error");

            numbers.push(bytes.slice(lenIdx + countSize).slice(0, nextBytes));
            lenIdx += nextBytes + countSize;
        }

        if (lenIdx != bytes.length)
            throw new Error("Format Error");

        return numbers;
    }

    /**
     * @param {bigInt.BigInteger[]} numbers 
     * @returns {Uint8Array}
     */
    static SerializeNumbers(numbers, countSize = 1)
    {
        return ByteSerializer.Serialize(numbers.map(num => new Uint8Array(num.toArray(256).value)), countSize);
    }

    /**
     * @param {Uint8Array[]} listBytes 
     * @returns {Uint8Array}
     */
    static Serialize(listBytes, countSize = 1)
    {
        var len = listBytes.reduce((sum, bytes) => sum + bytes.length, 0);
        var data = new Uint8Array(len + (listBytes.length * countSize) + 1);

        var i = 1;
        data[0] = listBytes.length;
        for (const num of listBytes) {
            data.set(toArray(num.length, countSize), i);
            data.set(num, i + countSize);
            i += num.length + countSize;
        }

        return data;
    }
}

/**
 * @param {Uint8Array} bytes
 * @returns {number}
 */
function ToInt(bytes)
{
    return BigInt.fromArray(Array.from(bytes), 256, false).valueOf();
}

/**
 * 
 * @param {number} number 
 * @param {number} byteSize 
 * @returns {number[]}
 */
function toArray(number, byteSize = null)
{
    var bytes = BigInt(number).toArray(256).value;
    if (bytes.length < byteSize)
        return utils.padLeft(bytes, byteSize - bytes.length);

    return byteSize > bytes.length ? bytes.slice(0, byteSize) : bytes;
}
