//best bignumber libraries jsbn big.js bn.js big-integer
import BN from 'bn.js';
import BigInt from 'big-integer';
import { randomBytes } from 'crypto';
import PHelp from './phelp';

export const constants = {
    zero: new BigInt(0),
    one: new BigInt(1),
    two: new BigInt(2)
};

export default class Utils {
    /**
     * 
     * @param {bigInt.BigInteger} value
     * @param {bigInt.BigInteger} p
     */
    static primeInv(value, p) {
        return value.modPow(p.minus(2), p);
    }
    
    /**
     * @param {bigInt.BigInteger} value 
     * @param {bigInt.BigInteger} divisor 
     */
    static mod(value, divisor) {
        return value.isNegative() ? value.add(divisor) : value.mod(divisor);
    }

    static encode(numbers, bits, encoding = undefined, fill = false) {
        const bytes = Utils.encodeByteArray(numbers, bits, fill);
        return Buffer.from(bytes).toString(encoding);
    }

    //TODO: Improve this to use Uint32Array and not waste to much memory
    static encodeByteArray(numbers, bits, fill = false) {
        const blockSize = Math.ceil(bits / 8);
        const arrayBuffer = [];

        for (let i = 0; i < numbers.length; i++) {
            const bytes = numbers[i].toArray(256).value;

            if (fill) { //fill the empty bits
                const diff = blockSize - bytes.length;
                for (let j = 0; j < diff; j++)
                    bytes.unshift(0);
            }

            arrayBuffer.push(bytes);
        }

        return Array.prototype.concat.apply([], arrayBuffer);
    }

    //TODO: Doesn't work with chinese characters, like text = "I'm 大戴禮記, and my dad's ابو عبد الله🤣";
    static decode(data, bits, encoding = undefined) {
        if (!Utils.isBuffer(data))
            data = Buffer.from(data, encoding);
    
        const blockSize = bits / 8;
        let numbers = [];
        let j = -1;
    
        for (let i = 0; i < data.length; i++) {
            if (i % blockSize === 0) {
                j++
                numbers.push(constants.zero)
            }
    
            numbers[j] = numbers[j].shiftLeft(8).or(new BigInt(data[i].toString()));
        }
        return numbers;
    }
    
    static getPrimitiveRoot(p) {
        if (p.equals(constants.two))
            return constants.one;
    
        const min = new BigInt(3); // Avoid g=2 because of Bleichenbacher's attack
        const max = p.subtract(constants.one);
        const p1 = constants.two; // The prime divisors of p-1 are 2 and (p-1)/2
        const p2 = max.divide(p1); // Because: p = 2q + 1 where q is a prime
    
        while (true) {
            const g = this.random(min, max);
            if (!g.modPow(p1, p).equals(constants.one) &&
                !g.modPow(p2, p).equals(constants.one) &&
                !p.subtract(constants.one).mod(g).equals(constants.zero) && // g|p-1
                !p.subtract(constants.one).mod(g.modInv(p)).equals(constants.zero)) // g^(-1)|p-1 (evades Khadir's attack)
                return g
        }
    }
    
    static random(min, max) {
        const rang = max.subtract(min);
        const rdmLen = Math.ceil(rang.bitLength() / 8)
        const diff = (rdmLen * 8) - rang.bitLength();
    
        let rdm;
        do {
            rdm = BigInt.fromArray(Array.from(randomBytes(rdmLen)), 256, false);
            rdm = rdm.shiftRight(diff).add(min);
        } while (rdm.greater(max));
    
        return rdm;
    }

    /** @param {number} bytes */
    static async randomPrimeBase(bytes, confidence = 160) {
        return PHelp.run(() => {
            const bits = bytes * 8;
            const mask = BigInt.one.shiftLeft(bits - 1).add(1);

            while (true) {
                const rdm = BigInt.fromArray(Array.from(randomBytes(bytes)), 256, false);
                var p = rdm.or(mask);
                if (!p.isProbablePrime())
                    continue;

                return p;
            }
        });
    }
    
    //min == 2 ^ (bits-2) == 1 << bits-2 that is, 1 and bits-2 zeros to the right
    //max == ((2 ^ bits) -2)/2 that is, all the bits in 1 with bits-1 length
    //because q = (p -1) / 2 and p <= (2 ^ bits) -1 that is, all the bits in 1
    static randomPrime(bits, confidence) {
        const maxLen = bits - 2;
        const rdmLen = Math.ceil(maxLen / 8);
        const diff = (rdmLen * 8) - maxLen;
        const min = constants.one.shiftLeft(bits - 2);
    
        while (true) {
            const rdm = BigInt.fromArray(Array.from(randomBytes(rdmLen)), 256, false);
            //trunk to bits, add minimum range and the number has to be odd
            const q = rdm.shiftRight(diff).add(min).or(constants.one);
            if (!q.isProbablePrime(confidence))
                continue;
    
            // p = (q * 2) + 1
            const p = q.shiftLeft(1).add(constants.one);
            if (!p.isProbablePrime(confidence))
                continue;
    
            return [p, q];
        }
    }

    static isBuffer(data) {
        return Buffer.isBuffer(data) || ArrayBuffer.isView(data)
    }
    
    /**
     * @param {number[] | Uint8Array} data
     * @param {number} length
     * @returns {number[]}
     */
    static padLeft(data, length) {
        const array = data instanceof Uint8Array ? Array.from(data) : data;
        
        return array.length >= length ? array : 
            new Array(length - array.length).fill(0).concat(array);
    }

    /**
     * @param {Uint8Array} arr1
     * @param {Uint8Array} arr2
     */
    static safeEqual(arr1, arr2) {
        var num1 = BigInt.fromArray(Array.from(arr1), 256, false);
        var num2 = BigInt.fromArray(Array.from(arr2), 256, false);

        return num1.xor(num2).eq(0);
    }
}
